.appButton{
    min-width: 86px;
    border-radius: 5px;
    background: #6E1E6C;
    color: white;
    padding: 8px 16px;
    font-size: 16px;
    border: 1px white solid;
}

.appButton:hover {
    background: #BF1891;
}

.appButton:focus {
    background-color: #E5CFDF;
    color:#6E1E6C;
}